.form-user {
  grid-column: 1 / -1;
}

.form-user__field input[type="checkbox"] + label,
.form-user__field input[type="radio"] + label {
  cursor: pointer;
}

.form-user__field input,
.form-user__field textarea {
  font-size: 20px;
  color: var(--back-input);
}

.form-user__note {
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-main);
  padding-right: 100px;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.form-user__note a {
  color: var(--basic-brand);
}

.form-user__btn {
  display: block;
  width: 312px;
  margin-left: auto;

  @media (max-width: 1600px) {
    width: 260px;
  }

  @media (max-width: 1280px) {
    width: 209px;
  }

