.application {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  max-width: 100%;
  min-height: 800px;
  background-color: #fff6ea;
  padding-top: 70px;
  padding-left: 190px;
  padding-right: 294px;
  padding-bottom: 82px;

  @media (max-width: 1600px) {
    min-height: 672px;
    padding-top: 60px;
    padding-left: 157px;
    padding-right: 243px;
    padding-bottom: 67px;
}

  @media (max-width: 1280px) {
    min-height: 537px;
    padding-top: 47px;
    padding-left: 126px;
    padding-right: 195px;
    padding-bottom: 54px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.application h3 {
  grid-column: span 2;
  font-family: inherit;
  font-size: 67px;
  font-variation-settings: 'wght' 700, 'wdth' 75;
  color: var(--basic-button);

  @media (max-width: 1600px) {
    font-size: 55px;
  }

  @media (max-width: 1280px) {
    font-size: 45px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.application > p {
  position: relative;
  display: grid;
  grid-column: span 2;
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-brand);
  padding-left: 35px;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.application > p::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 27px;
  top: -15px;
  left: 0;
  background-image: url("../img/star.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 23px;
    height: 21px;
  }

  @media (max-width: 1280px) {
    width: 18px;
    height: 15px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.application__form {
  margin-top: 45px;
}

