.field-item {

}

.field-item:not(.field-item--comment) {
  margin-bottom: 48px;

  @media (max-width: 1600px) {
    margin-bottom: 40px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 32px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item label {
  display: block;
}

.field-item legend,
.field-item label {
  font-family: inherit;
  font-size: 34px;
  color: var(--basic-main);
  margin-bottom: 10px;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item input,
.field-item textarea {
  background: transparent;
  padding: 10px;
}

.field-item--profile {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 185px;
  row-gap: 6vw;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 767px) {
    text-align: center;
  }
}

.field-profiles__item {
  margin: 0;
}

.field-item--choice {
  display: flex;
  align-items: center;
}

.field-item--choice p {
  font-family: inherit;
  font-size: 34px;
  margin-right: 37px;

  @media (max-width: 1600px) {
    font-size: 28px;
    margin-right: 31px;
  }

  @media (max-width: 1280px) {
    font-size: 21px;
    margin-right: 25px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item--choice label {
  position: relative;
  font-size: 25px;
  padding-left: 50px;
  margin-right: 86px;
  margin-bottom: 0;

  @media (max-width: 1600px) {
    font-size: 21px;
    padding-left: 42px;
    margin-right: 34px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    padding-left: 32px;
    margin-right: 28px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item--choice input[type="radio"] + label::before {
  position: absolute;
  display: inline-block;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: transparent;
  border: 1px solid var(--basic-main);

  vertical-align: middle;

  @media (max-width: 1600px) {
    width: 29px;
    height: 29px;
  }

  @media (max-width: 1280px) {
    width: 23px;
    height: 23px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item--choice input[type="radio"]:checked + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background-color: var(--basic-brand);
  background: url("../img/chekbox-on.jpg");
  background-repeat: no-repeat;
  background-size: 25px 25px;
  background-position: 50% 50%;

  @media (max-width: 1600px) {
    width: 29px;
    height: 29px;
    background-size: 21px 21px;
  }

  @media (max-width: 1280px) {
    width: 23px;
    height: 23px;
    background-size: 17px 17px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item--choice ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.field-item--profile input[type="text"],
.field-item--profile input[type="tel"] {
  width: 100%;
  height: 34px;
  border: none;
  border-bottom: 2px solid var(--basic-main);
}

.field-item--comment {
  margin-bottom: 38px;

  @media (max-width: 1600px) {
    margin-bottom: 32px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 24px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.field-item--comment legend {
  margin-bottom: 18px;
}

.field-item--comment textarea {
  width: 100%;
  min-height: 155px;
  border: 2px solid var(--basic-main);
  resize: vertical;

  @media (max-width: 1600px) {
    min-height: 128px;
  }

  @media (max-width: 1280px) {
    min-height: 100px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

