.menu {
  width: 1040px;
  display: grid;
  grid-template-columns: repeat(6, max-content);
  column-gap: 1.4vw;
  align-items: center;

  @media (max-width: 1600px) {
    width: 870px;
  }

  @media (max-width: 1280px) {
    width: 670px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}

.menu li {
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-weight: 300;
  letter-spacing: -0.5px;
  font-variant: small-caps;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }

  &:hover {
    font-size: 25px;
    text-transform: uppercase;

    @media (max-width: 1600px) {
      font-size: 21px;
    }

    @media (max-width: 1280px) {
      font-size: 17px;
    }

    @media (max-width: 992px) {

    }

    @media (max-width: 767px) {

    }

    @media (max-width: 480px) {

    }

    &:active {
      opacity: 0.5;
    }
  }
}

.menu a {
  color: var(--basic-white);
  text-decoration: none;
}

a.menu__link-active {
  font-size: 25px;
  text-transform: uppercase;
  color: var(--basic-main);
  padding-bottom: 6px;
  cursor: default;
  border-bottom: 4px solid var(--basic-main);

  @media (max-width: 1600px) {
    font-size: 21px;
    padding-bottom: 4px;
    border-bottom: 3px solid var(--basic-main);
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    border-bottom: 2px solid var(--basic-main);
  }

  @media (max-width: 992px) {

  }

  @media (max-width: 767px) {

  }

  @media (max-width: 480px) {

  }
}
