.item-product__parameters span {
  position: relative;
  display: inline-block;
  margin-right: 5.73vw;
  margin-top: 50px;
}

.item-product__parameters span::after {
  position: absolute;
  content: "";
  width: 70px;
  height: 77px;
  top: 50%;
  transform: translateY(-50%);
  left: 2.7vw;
  background-image: url("../img/work1.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 60px;
    height: 67px;
  }

  @media (max-width: 1280px) {
    width: 47px;
