.item-product__buy {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  align-items: center;
  font-family: inherit;
  margin-top: 57px;
}

.item-product__buy p {
  color: var(--basic-white);
  margin-left: 40px;
}

